@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "source-code-pro", "Menlo, Monaco, Consolas", "Courier New",
    monospace;
}

::-webkit-scrollbar {
  display: none;
}

@layer components {
  .viewContainer {
    @apply container mx-auto max-w-[95%] md:max-w-[85%];
  }

  .btn-primary {
    @apply block text-center py-2 px-8 sm:px-10 bg-colors-primary rounded text-white font-bold cursor-pointer
    hover:bg-colors-sixth 
    focus:bg-colors-seventh 
    disabled:opacity-50 disabled:cursor-not-allowed;
  }

  .btn-secondary {
    @apply block text-center py-2 px-10 bg-colors-fifth rounded text-black font-bold hover:bg-colors-sixth focus:bg-colors-seventh;
  }

  .btn-linePay {
    @apply block text-center py-2 px-10 bg-line-pay rounded text-white font-bold hover:bg-colors-sixth focus:bg-colors-seventh;
  }

  .btn-red {
    @apply block text-center py-2 px-8 sm:px-10 bg-red-700 rounded text-white font-bold hover:bg-red-700/70 focus:bg-colors-seventh;
  }

  .btn-google {
    @apply flex justify-center py-3 rounded text-gray-700 gap-5;
  }

  .btn-addPayment {
    @apply block text-center py-2 px-8 sm:px-10 border-2 border-colors-primary rounded text-colors-primary font-bold hover:border-white hover:bg-colors-sixth hover:text-white focus:bg-colors-seventh;
  }

  .btn-outline {
    @apply block text-center py-2 px-6 bg-white border border-colors-primary rounded text-colors-primary font-bold hover:bg-colors-sixth hover:text-white hover:border-white focus:bg-colors-seventh;
  }

  .btn-outline-fifth {
    @apply block text-center py-2 px-6 bg-white border border-colors-fifth rounded font-bold hover:bg-colors-sixth focus:bg-colors-seventh;
  }

  .btn-outline-linepay {
    @apply block text-center py-2 px-6 bg-white border border-line-pay rounded font-bold hover:bg-colors-sixth focus:bg-colors-seventh;
  }

  .btn-outline-red {
    @apply block text-center py-2 px-6 bg-white border border-red-700 rounded font-bold hover:bg-colors-sixth focus:bg-colors-seventh;
  }

  .footerLink {
    @apply flex flex-col items-center;
  }

  .footerImg {
    @apply lg:hidden mb-2 lg:mb-0;
  }

  .footerImg-phone {
    @apply mb-2;
  }

  .footerTxt {
    @apply text-xs md:text-base;
  }

  .headerNav-hover {
